import React from 'react';

function Card(props){
    return(
        <div className="card">
            <img className="card__imagem" src={props.source} alt={props.title}></img>
            <div className="card__corpo">
                <h2 className="card__titulo">{props.titulo}</h2>
                <p className="card__descricao">{props.descricao}</p>
                <a className="card__button" href={props.link} target={'_blank'} rel={'noopener noreferrer'}>Visit Website</a>
            </div>
        </div>
    )
}

export default Card;