import React from 'react';
import './Sobre.css';
import Container from '@material-ui/core/Container';

function Sobre(){
    return(
        <Container maxWidth='md'>
            <div className="sobre">
                <h1>Front-end Developer</h1>
                <p>I solve front-end web problems</p>
                <h2>Hi, I’m José Victor. Nice to meet you.</h2>
            </div>
        </Container>
    )
}

export default Sobre;